* {
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
}
@font-face {
  font-family: "Inter Black";
  src: url(./fonts/Inter-Black.ttf);
}
@font-face {
  font-family: "Inter Bold";
  src: url(./fonts/Inter-Bold.ttf);
}
@font-face {
  font-family: "Inter Light";
  src: url(./fonts/Inter-Light.ttf);
}
@font-face {
  font-family: "Inter Regular";
  src: url(./fonts/Inter-Regular.ttf);
}
@font-face {
  font-family: "Inter Thin";
  src: url(./fonts/Inter-Thin.ttf);
}
.app {
}
.navbar {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(38, 71, 0);
}
.navbar > .left {
}
.left > a {
  font-size: 40px;
  font-family: "Inter Bold";
  color: bisque;
  text-decoration: none;
}
.middle h3 {
  font-family: "Inter Light";
  color: bisque;
}
.navbar > .right{
  display: flex;
}

.content h1{
  font-family: "Inter Light";

}

.parent {display: block;position: relative;float: left;line-height: 30px;background-color: #4FA0D8;border-right:#CCC 1px solid;}
.parent a{margin: 10px;color: #FFFFFF;text-decoration: none;}
.parent:hover > ul {display:block;position:absolute; cursor: pointer;}
.child {display: none;}
.child li {background-color: #E4EFF7;line-height: 30px;border-bottom:#CCC 1px solid;border-right:#CCC 1px solid; width:100%; cursor: pointer;}
.child li a{color: #000000; }
ul{list-style: none;margin: 0;padding: 0px; min-width:10em; cursor: pointer;}
ul ul ul{left: 100%;top: 0;margin-left:1px; cursor: pointer; }
li:hover {background-color: rgb(38, 71, 0); cursor: pointer;}
.parent li:hover {background-color: #F0F0F0; cursor: pointer;}
.expand{font-size:12px;float:right;margin-right:5px; }

.card-box {
  padding: 30px 30px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.herocard{
  display: inline-block;
}
.card-box > .card {
  height: 480px;
  width: 270px;
  border-radius: 4px;
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;

}
.card-box > .card:hover {
  box-shadow: 2px 3px 5px 0 rgb(0 0 0 / 20%);
  transform: scale(1.05);
  transition: 0.3s;
}

.card > .top > img {
  border-radius: 4px;
  width: 270px;
  margin: auto;
  object-fit: contain;
}

.card > .bottom {
  padding-top: 7px;
  padding-left: 7px;
  padding-right: 7px;
}

.bottom > h1 {
  font-size: 1.2rem;
  font-family: "Montserrat", sans-serif;
}
.bottom > h3 {
  font-family: "Montserrat", sans-serif;
  padding-bottom: 5px;
  font-size: small;
}
.bottom > h2 {
  padding-bottom: 3px;
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
}

.bottom > .price {
  border: 1px solid #000814;
  padding: 20px;
  border-radius: 4px;
}

.price > ul > li {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
}

.bottom > .price:hover {
  background-color: rgb(38, 71, 0) !important;
  color: #fff !important;
  border-color:rgb(38, 71, 0) !important;
  transform: scale(1.05);
  transition: 0.3s;
}

.bottom > h2 {
  font-family: "Montserrat", sans-serif;
  padding-top: 7px;
  font-size: 1rem;
}

